const CourseItem = ({
    json
}: {
    json: any
}) => {
    const isOwner = !['needLogin', 'needPurchase'].includes(fbase.isSubscriptionInfo());
    // const isOwner: boolean = true;

    return (
        <>

            <li className="course-item" onClick={() => {
                data.gotoPage('CourseMain', json.query);
            }}>
                <div className={classnames(["cover-img", css`
                background-image: url("${data.getRootPath()}resource/courses/${json.courseID}/cover.jpg");
            `])}>
                </div>
                <div className="info">
                    <h3>{json.title}</h3>
                    <div className="info-sub">
                        <div className="course-num">{json.item.length}개 코스</div>
                        <Tags str={json.tags} key={json.courseID} />
                        {/* <div className="btns-func">
                            {data.numberWithCommas(parseInt(json.prices[0]))}원
                            {
                                isOwner && <span>구매완료</span>
                            }
                        </div> */}

                    </div>


                </div>
            </li>
        </>

    )
}

export const Tags = ({
    str
}: {
    str: string
}) => {
    const arr = str.split(',');
    let Tag = arr.map((item: string, i: number) => {
        return <span className="tag" key={i}>#{item}</span>
    });
    if (str === '') {
        Tag = [];
    }
    return (
        <div className="Tags">
            {Tag}
        </div>
    );
}

export default PageHome;